body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@charset "UTF-8";
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0; }

body {
  background: #f0f2f5; }

/* 头部 */
.ant-layout-header {
  position: fixed;
  width: 100%;
  z-index: 1;
  background: #ffffff;
  padding: 0; }
  .ant-layout-header .logo {
    float: left;
    padding-left: 100px;
    padding-right: 60px; }
  .ant-layout-header .ant-menu-horizontal {
    width: 100%;
    margin: auto;
    height: 64px;
    line-height: 64px;
    border-bottom: none; }
  .ant-layout-header .smallmenuImg {
    float: left;
    width: 32px;
    margin-top: 20px;
    margin-right: 20px; }
  .ant-layout-header .serach {
    width: 300px; }

/* 卡片 */
.cardlist {
  padding: 3em; }

.chainID {
  word-break: break-all; }

.card {
  margin-top: 60px; }
  .card .cardtwo {
    padding: 0 3em; }
  .card span {
    color: rgba(0, 0, 0, 0.45);
    font-size: 14px;
    line-height: 22px; }
  .card h3 {
    font-size: 30px;
    line-height: 38px;
    color: #000000;
    padding: 8px; }
  .card .fibos_eos {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap; }

/* 标签页 */
.tabs {
  background: #ffffff;
  margin: 3em;
  padding: 1em; }

/* 表单 */
.account, .accountsdetails, .reward {
  margin: 100px 40px 20px 40px;
  min-height: 70vh; }
  .account .creatname, .accountsdetails .creatname, .reward .creatname {
    margin-left: 15px; }

/* 账户详情 */
.accountsdetails {
  padding-top: 10px; }

/* 资产 */
.accounts {
  padding: 0 50px;
  padding-bottom: 20px;
  background: #ffffff; }
  .accounts .ant-collapse-content > .ant-collapse-content-box {
    padding: 16px 0; }
  .accounts .details {
    padding: 20px 50px;
    border-bottom: 1px solid #f0f0f0; }
    .accounts .details .created {
      margin-left: 20px;
      font-size: 14px;
      color: #888888; }

.accountsdetails .balance {
  color: #666666;
  line-height: 14px;
  font-size: 14px; }

.accountsdetails .num {
  margin-top: 20px;
  font-size: 32px;
  font-weight: 500;
  line-height: 30px;
  height: 30px;
  color: #333333; }

.accountsdetails .mortgage {
  font-weight: bold; }

.accountsdetails .mortgagenum {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  padding: 10px 0; }

.accountsdetails .fo {
  font-size: 24px;
  font-weight: bold;
  color: #3080fe; }

.accountsdetails .number {
  color: #999999; }

.accountsdetails .resources {
  padding-top: 20px;
  color: #999999; }

.accountsdetails .currency, .accountsdetails .permissions, .accountsdetails .actions {
  margin-top: 40px;
  padding: 20px 50px;
  background: #ffffff; }
  .accountsdetails .currency .detail, .accountsdetails .permissions .detail, .accountsdetails .actions .detail {
    width: 80px; }

pre {
  white-space: pre-wrap;
  word-wrap: break-word; }

/* 脚步 */
.footer {
  text-align: center;
  margin: 3em 0 1em 0; }
  .footer p {
    text-align: center;
    color: rgba(0, 0, 0, 0.45);
    padding: 5px;
    font-size: 12px; }
    .footer p a {
      color: rgba(0, 0, 0, 0.45);
      margin: 0 20px; }

@media (max-width: 768px) {
  .ant-menu-submenu-arrow {
    right: 0 !important; }
  .ant-card {
    margin-bottom: 20px; }
  .ant-layout-header .logo {
    padding-left: 20px;
    padding-right: 20px; }
  .ant-layout-header .serach {
    margin-left: 40px;
    width: 150px; }
  .card .cardtwo {
    padding: 0 1em; }
  .cardlist {
    padding: 1em; }
  .accountsdetails .accounts, .accountsdetails .currency, .accountsdetails .permissions, .accountsdetails .actions {
    padding: 20px 20px; } }

@media (max-width: 576px) {
  .ant-layout-header .serach {
    margin-left: 40px;
    width: 125px; }
  .account .creatname, .accountsdetails .creatname, .reward .creatname {
    margin-left: 0; } }

